     }
      .popup {
        display: flex;
        align-items: center;
        justify-content: center;
        position: fixed;
        width: 100vw;
        height: 100vh;
        bottom: 0;
        right: 0;
        background-color: rgba(0, 0, 0, .80);
        z-index: 2;
        visibility: hidden;
        opacity: 0;
        overflow: hiden;
        transition: 0.64s ease-in-out;
     }
      .popup-inner {
        position: relative;
        bottom: -100vw;
        right: -100vh;
        display: flex;
        flex-direction:column;
        align-items: center;
        max-width: 800px;
        max-height: 600px;
        width: 60%;
        height: 80%;
        background-color: #fff;
        transform: rotate(32deg);
        transition: 0.64s ease-in-out;
     }
      /* .popup__photo {
        display: flex;
