      .popup__text {
        display: flex;
        flex-direction: row;
        justify-content:left;
        width: 700px;
        height: 480px;
        padding: 0px;
        flex-wrap:wrap;
        margin-left:70px;
     }
      .popup__text h1 {
        font-size: 2rem;
        font-weight: 600;
        margin-bottom: 2rem;
        text-transform: uppercase;
        color: #0a0a0a;
     }
      .popup__text p {
        font-size: 0.875rem;
        color: #686868;
        line-height: 1.5;
     }
      .popup:target {
        visibility: visible;
        opacity: 1;
     }
      .popup:target .popup-inner {
        bottom: 0;
        right: 0;
        transform: rotate(0);
     }
      .popup__close {
        position: absolute;
        right: -1rem;
        top: -1rem;
        width: 3rem;
        height: 3rem;
        font-size: 0.875rem;
        font-weight: 300;
        border-radius: 100%;
        background-color: #0a0a0a;
        z-index: 4;
        color: #fff;
        line-height: 3rem;
        text-align: center;
        cursor: pointer;
        text-decoration: none;
     }
      
